import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)
// 准备actions

// 去除驼峰命名
let removeCamelCase = function (str) {
    const match = str.match(/[A-Z][a-z]*/);
    return match ? match[0].toLowerCase() : str;
}   

const actions = {  
    // Action中的函数接受一个精简版的 store，因此你可以调用 context.commit 提交一个 mutation


}
// dao
const mutations = {
    // mutations中的函数默认接收数据对象 state
    SETUSER(state, user){
        let obj = {}
        console.log(user);
        for(let k in user){
            let rout = k.slice(0,3);
            if(rout === "stu" || rout === 'tea'){
                let key = removeCamelCase(k);
                obj[key] = user[k]
            }else {
                obj[k] = user[k]
            }
        }
        console.log(obj);   
        state.user = obj;
    },
    CLEANUSER(state,){
        state.user = null;
    }
}
// 准备数据
const state = {
    // user:null,
    user:{
        birthday:"2009-06-03",
        calss:"软件技术",
        card:"310110199901010101",
        id:"1",
        img:null,
        mail:"zhangsan@example.com",
        name:"张三",
        password:"1",
        phone:"13812345678",
        role:"0",
        sex:1,
    }
}

export default new Vuex.Store({
    actions,
    mutations,
    state
})