<template>
    <div class="box">
        <el-form ref="form" :inline="true" :model="form" class="demo-form-inline form_box" >
            <div>
                <el-form-item label="学生名">
                 <el-col :span="40">
                    <el-input v-model="form.teaName" style="width: 100%;"></el-input>
                 </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </div>
            <div>
                <el-form-item>
                    <el-button type="primary" @click="onCreate">新建</el-button>
                </el-form-item>
            </div>
        </el-form>

        <el-table :data="mentList.slice((currentPage - 1) * pagesize, currentPage * pagesize)" 
            style="width:100%;font-size: 12px;" >

                 <el-table-column label="id号" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.id}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="学生名" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px;">{{scope.row.stuName}}</span>
                    </template>
                </el-table-column>

               <el-table-column label="老师名" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.teaName}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="预约时间" >
                    <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.time}}</span>
                        </template>
                </el-table-column>

                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button 
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)" type="primary" icon="el-icon-info" circle ></el-button>
                        <el-button 
                            size="mini"
                            @click="handleDelete(scope.$index, scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 15, 20]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="mentList.length">
             </el-pagination> 


            <el-dialog
            title="预约信息详情"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            
            <el-form status-icon :model="ment" ref="modifyForm" label-width="70px" class="demo-ruleForm">

                
                <el-form-item label="id号" prop="pass" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="ment.id"
                        autocomplete="off" class="txt_input"></el-input>
                    </el-col>
                </el-form-item>

                <el-form-item label="学生名" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="ment.stuName"
                    autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item label="教师名" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="ment.teaName"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="预约时间" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="ment.time"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="备注" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="ment.notes"
                    autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="状态" prop="pass" style="width:95%">
                    <el-select v-model="ment.state" >
                        <el-option label="未受理" :value="0"></el-option>
                        <el-option label="预约失败" :value="1"></el-option>
                        <el-option label="预约成功" :value="2"></el-option>
                    </el-select>
                </el-form-item>
                
            </el-form>
        </el-dialog> 
    </div>
</template>

<script>
import API from '@/api/studentApi'

export default {
  name: 'PsyAppointment',
  data() {
    return {
      form:{
            teaName:""
        },
        mentList:[],
         currentPage: 1, //初始页
        pagesize: 10, //    每页的数据,
        dialogVisible:false,
        ment:{
            id:'',
            notes:'',
            state:'',
            stuId:'',
            stuName:'',
            teaId:'',
            teaName:'',
            time:'',
        }
    };
  },

  mounted() {
    this.getMentAll();
  },

  methods: {
    onSubmit(){
        console.log(this.form);
        // if(this.form.teaName != ''){
        //     API.getTeaConditions(this.form).then(({data}) => {
        //         console.log(data);
        //         this.mentList = data.data
        //     })  
        // }else {
        //     this.getTeacherAll();
        // }
    },
    onCreate(){
      
    },
    getMentAll(){
        API.getMentAll().then(({data})=>{
            console.log(data);
            this.mentList = data.data
        })
    },
      handleSizeChange (size) {
            console.log(size,'size');
            this.pagesize = size;
            console.log(this.pagesize); //每页下拉显示数据
        },
        handleCurrentChange (currentPage) {
            console.log(currentPage,'currentPage');
            this.currentPage = currentPage;
            console.log(this.currentPage); //点击第几页
        },
          handleClose(){
            let yuMent = null;
            this.mentList.forEach(obj =>{
                if(obj.id === this.ment.id){
                    yuMent = obj
                }
            })
            console.log("###",yuMent);
            console.log(this.ment.state , yuMent.state,this.ment.state == yuMent.state);
            if(this.ment.state != yuMent.state){
                API.updateState(this.ment.id,this.ment.state).then(({data})=>{
                    this.$message({message:data.msg,type:"success"})
                    yuMent.state = this.ment.state
                })
            }
            this.dialogVisible = false;
        }, 
        handleEdit(index, row) {
            // console.log(index, row);
            this.dialogVisible = true;
            this.ment = {...row}
            // console.log(this.ment);
        },
        handleDelete(index, row) {
            console.log(index, row);
            // API.deleteTea(row.teaId).then(({data})=>{
            //     console.log(data);
            //     this.$message({message:data.msg,type:"success"})
            // })
            // let key = this.mentList.findIndex(item => item.teaId === row.teaId);
            // if (key !== -1) {
            //     this.mentList.splice(key, 1);
            // }
        },
  },
};
</script>

<style scoped>
.box {
    margin-top: 20px;
    padding: 0 30px;
}
.form_box {
    display: flex;
        justify-content: space-between;
}
</style>