<template>
 
    <div class="box">
        <el-form ref="form" :inline="true" :model="form" class="demo-form-inline form_box" >
            <div>
                <el-form-item label="编辑人">
                <el-col :span="40">
                    <el-input v-model="form.teaName" style="width: 100%;"></el-input>
                </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </div>
            <div>
                <el-form-item>
                    <el-button type="primary" @click="onCreate">新建</el-button>
                </el-form-item>
            </div>
        </el-form>

        <!-- 表格 -->
        <div>
            <el-table :data="notList.slice((currentPage - 1) * pagesize, currentPage * pagesize)" 
        style="width:100%;font-size: 12px;" >
                 <el-table-column label="内容" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.content}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="开始日期" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px;">{{scope.row.startDate}}</span>
                    </template>
                </el-table-column>

               <el-table-column label="结束日期" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.endDate}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="编辑人" >
                    <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.initiatorName}}</span>
                        </template>
                </el-table-column>

                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button 
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)" type="primary" icon="el-icon-info" circle ></el-button>
                        <el-button 
                            size="mini"
                            @click="handleDelete(scope.$index, scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
                    </template>
                </el-table-column>
            </el-table>
             <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 15, 20]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="notList.length">
             </el-pagination>  
        </div>

         <el-dialog
            title="通知信息详情"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            
            <el-form status-icon :model="notice" ref="modifyForm" label-width="70px" class="demo-ruleForm">
                <!-- <div class="block"><el-avatar shape="square" :size="50" :src="squareUrl"></el-avatar></div> -->
                
                <el-form-item label="内容" prop="pass" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="notice.content"
                        autocomplete="off" class="txt_input"></el-input>
                    </el-col>
                </el-form-item>

                <el-form-item label="时间段" prop="pass" style="width:95%">
                  <div class="block" >
                    <!-- <span class="demonstration">默认</span> -->
                    <el-date-picker
                    style="width:100%"
                    v-model="timePeriod"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd">
                    </el-date-picker>
                </div>
                </el-form-item>

                <el-form-item label="编辑时间">
                    <el-col :span="13">
                    <el-date-picker type="date" 
                        placeholder="选择日期" 
                        v-model="notice.editDate" 
                        style="width: 100%;"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd"></el-date-picker>
                    </el-col>
                </el-form-item> 

                <el-form-item label="编辑人" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="notice.initiatorName"
                    autocomplete="off"></el-input>
                </el-form-item>       

            </el-form>

        </el-dialog> 



        <!-- 添加部分 -->
        <el-dialog
            title="通知信息添加"
            :visible.sync="dialoCreateVisible"
            width="30%"
            :before-close="handleCreateClose">
            
            <el-form status-icon :model="notice" ref="modifydddForm" label-width="70px" class="demo-ruleForm" :rules="rules">
                <!-- <div class="block"><el-avatar shape="square" :size="50" :src="squareUrl"></el-avatar></div> -->
                
                <el-form-item label="内容" prop="content" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="notice.content"
                        autocomplete="off" class="txt_input"></el-input>
                    </el-col>
                </el-form-item>

                <el-form-item label="时间段" style="width:95%">
                  <div class="block" >
                    <!-- <span class="demonstration">默认</span> -->
                    <el-date-picker
                    style="width:100%"
                    v-model="timePeriod"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd">
                    </el-date-picker>
                </div>
                </el-form-item>


            </el-form>

        </el-dialog> 
    </div>
</template>

<script>
import API from '@/api/studentApi'

export default {
  name: 'noticeManage',
    computed:{
        timePeriod:{
            get:function(){
                return [this.notice.startDate,this.notice.endDate]
            },
            set:function(data){
                console.log(data);
                this.notice.startDate = data[0]
                this.notice.endDate = data[1]
            }
        }
    },  
  data() {
    return {
        value1:"",
        form:{
            teaName:""
        },
        notList:[],
        currentPage: 1, //初始页
        pagesize: 10, //    每页的数据,
        dialogVisible:false,
        dialoCreateVisible:false,
        notice:{
            content:'',
            editDate:'',
            endDate:'',
            id:'',
            initiatorId:'',
            initiatorName:'',
            startDate:'',
        },
        rules: {
            content:[{ required: true, message: '请输入内容', trigger: 'blur' }],
        }
    };
  },

  mounted() {
    this.getNoticeList()
  },

  methods: {
    onSubmit(){
        console.log(this.form);
        if(this.form.teaName != ''){
            API.getNoticeByName(this.form.teaName).then(({data}) => {
                console.log(data);
                this.notList = data.data
            })  
        }else {
            this.getNoticeList();
        }
        
    },
    onCreate(){
        this.dialoCreateVisible = true;
    },
    handleEdit(index, row) {
        console.log(index, row);
        this.dialogVisible = true;
        this.notice = row

        console.log(this.notice);
    },
    handleDelete(index, row) {
        console.log(index, row);
        API.deleteNot(row.id).then(({data})=>{
            console.log(data);
            this.$message({message:data.msg,type:"success"})
        })
        // 根据id删除对象数组中的对象
        let key = this.notList.findIndex(item => item.id === row.id );
        if (key !== -1) {
            this.notList.splice(key, 1);
        }
    },
    getNoticeList(){
        API.getNotAll().then(({data}) => {
            console.log(data.data);
            this.notList = data.data
        })
    },
     handleClose(){
        this.dialogVisible = false;
        this.notice = {...this.initialization()}
    }, 
    handleCreateClose(){
        // this.dialoCreateVisible = false;
         this.$refs['modifydddForm'].validate((valid) => {
        if (valid) {
            this.dialoCreateVisible = false;
            this.notice.editDate = this.formatDate(new Date())
            this.notice.initiatorId = this.$store.state.user.id
            this.notice.initiatorName = this.$store.state.user.name
            API.addNotice(this.notice).then(({data})=>{
                if(data.code === 200)this.$message({message:data.msg,type:"success"})
                else this.$message({message:data.msg,type:"error"})
                this.getNoticeList()
            })
        } else {
            console.log('error submit!!');
            this.dialoCreateVisible = false;
            this.notice = {...this.initialization()}
            return false;
        }
    }); 
    }, 
    initialization(){
        return{ 
            content:'',
            editDate:'',
            endDate:'',
            id:'',
            initiatorId:'',
            initiatorName:'',
            startDate:'',
        }
    },
    formatDate:function(date) {
        var year = date.getFullYear();
        var month = ("0" + (date.getMonth() + 1)).slice(-2); // 月份从0开始，所以需要+1，然后补0
        var day = ("0" + date.getDate()).slice(-2); // 日期补0
        var hours = ("0" + date.getHours()).slice(-2); // 小时补0
        var minutes = ("0" + date.getMinutes()).slice(-2); // 分钟补0
        var seconds = ("0" + date.getSeconds()).slice(-2); // 秒补0
        
        return year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
    }

  },
};
</script>

<style scoped>
.box {
    margin-top: 20px;
    padding: 0 30px;
}
.form_box {
    display: flex;
        justify-content: space-between;
}

.el-table /deep/ .cell{
    white-space: nowrap;          
    overflow: hidden;                 
    text-overflow: ellipsis;         
}
</style>