<template>
    <div class="box">
        <el-form ref="form" :inline="true" :model="form" class="demo-form-inline form_box" >
            <div>
                <el-form-item label="姓名">
                 <el-col :span="40">
                    <el-input v-model="form.stuName" style="width: 100%;"></el-input>
                 </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </div>
            <div>
                <el-form-item>
                    <el-button type="primary" @click="onCreate">新建</el-button>
                </el-form-item>
            </div>
        </el-form>
        
        <!-- 表格 -->
        <div>
            <el-table :data="stuList.slice((currentPage - 1) * pagesize, currentPage * pagesize)" 
        style="width:100%;font-size: 12px;" >

                 <el-table-column label="学号" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.stuId}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="姓名" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px;">{{scope.row.stuName}}</span>
                    </template>
                </el-table-column>

               <el-table-column label="身份证" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.card}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="班级" >
                    <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.stuCalss}}</span>
                        </template>
                </el-table-column>

                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button 
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)" type="primary" icon="el-icon-info" circle ></el-button>
                        <el-button 
                            size="mini"
                            @click="handleDelete(scope.$index, scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
                    </template>
                </el-table-column>
            </el-table>
             <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 15, 20]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="stuList.length">
             </el-pagination>  

        </div>

        <!-- 详细信息 -->
        <el-dialog
            title="学生信息详情"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            
            <el-form status-icon :model="book" ref="modifyForm" label-width="60px" class="demo-ruleForm">
                <div class="block"><el-avatar shape="square" :size="50" :src="squareUrl"></el-avatar></div>
                
                <el-form-item label="姓名" prop="stuName" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="student.stuName"
                        autocomplete="off" class="txt_input"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="性别" prop="stuSex" style="width:95%">
                    <el-select v-model="student.stuSex" style="width:95%">
                        <el-option label="男" :value="0"></el-option>
                        <el-option label="女" :value="1"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="学号" prop="stuId" style="width:95%">
                    <el-input type="txt" v-model="student.stuId"
                    autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item label="身份证" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="student.card"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="班级" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="student.stuCalss"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="student.mail"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="年月日" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="student.stuBirthday"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="电话" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="student.stuPhone"
                    autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
        </el-dialog>


        <!-- 学生添加信息 -->
        <el-dialog
            title="添加学生"
            :visible.sync="dialogCreateVisible"
            width="30%"
            :before-close="handleCreateClose">
            
            <el-form status-icon :model="student" ref="createStudentRef" label-width="60px" class="demo-ruleForm" :rules="rules">
                <div class="block"><el-avatar shape="square" :size="50" :src="squareUrl"></el-avatar></div>
                
                <el-form-item label="姓名" prop="stuName" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="student.stuName"
                        autocomplete="off" class="txt_input"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="性别" prop="stuSex" style="width:95%">
                    <el-select v-model="student.stuSex" style="width:95%">
                        <el-option label="男" :value="0"></el-option>
                        <el-option label="女" :value="1"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="学号" prop="stuId" style="width:95%">
                    <el-input type="txt" v-model="student.stuId"
                    autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="stuPassword" style="width:95%">
                    <el-input type="txt" v-model="student.stuPassword"
                    autocomplete="off" ></el-input>
                </el-form-item>
                <el-form-item label="身份证" style="width:95%">
                    <el-input type="txt" v-model="student.card"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="班级"  style="width:95%">
                    <el-input type="txt" v-model="student.stuCalss"
                    autocomplete="off"></el-input>
                </el-form-item>
                <!-- <el-form-item label="邮箱" style="width:95%">
                    <el-input type="txt" v-model="student.mail"
                    autocomplete="off"></el-input>
                </el-form-item> -->
                <el-form-item label="年月日"  style="width:95%">
                    <el-input type="txt" v-model="student.stuBirthday"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="电话"  style="width:95%">
                    <el-input type="txt" v-model="student.stuPhone"
                    autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import API from '@/api/studentApi'
export default {
  name: 'StudentManage',
computed: {
    formattedSex: {
      get() {
        if(this.student.stuSex === 0 ){
            return '男'
        }else if(this.student.stuSex === 1){
            return '女'
        }else {
            return '';
        }
      },
      set(value) {
        this.student.stuSex = value === '男' ? 0 : 1;
      }
    }
  },
  data() {
    return {
        form:{
            stuName:""
        },
        stuList:[],
        // 分页
        currentPage: 1, //初始页
        pagesize: 10, //    每页的数据,
        // 有哪些书架 名/id
        shelfList:[],
        dialogVisible:false,
        dialogCreateVisible:false,
        student:{
            card:"",
            img:"",
            mail:"",
            stuBirthday:"",
            stuCalss:"",
            stuId:"",
            stuName:"",
            stuPassword:"",
            stuPhone:"",
            stuSex:"",
        },
         squareUrl: "https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png",

        rules: {
            stuName:[{ required: true, message: '请输入姓名', trigger: 'blur' }],
            stuSex:[{ required: true, message: '请选择性别', trigger: 'blur' }],
            stuId:[{ required: true, message: '请输入学号', trigger: 'blur' }],
            stuPassword:[{ required: true, message: '请输入密码', trigger: 'blur' }],
        }
    };
  },

  mounted() {
    this.getStudentList()
  },    
  methods: {
        handleSizeChange (size) {
            console.log(size,'size');
            this.pagesize = size;       
            console.log(this.pagesize); //每页下拉显示数据
        },
        handleCurrentChange (currentPage) {
            console.log(currentPage,'currentPage');
            this.currentPage = currentPage;
            console.log(this.currentPage); //点击第几页
        },
        
        handleEdit(index, row) {
            console.log(index, row);
            this.dialogVisible = true;
            this.student = row
            console.log(this.student);
        },
        handleDelete(index, row) {
            console.log(index, row);
            API.deleteStu(row.stuId).then(({data})=>{
                console.log(data);
                this.$message({message:data.msg,type:"success"})
            })
            // 根据id删除对象数组中的对象
            let key = this.stuList.findIndex(item => item.stuId === row.stuId);
            if (key !== -1) {
                this.stuList.splice(key, 1);
            }
        },
        getStudentList(){
            API.getStuAll().then(({data}) =>{
                    console.log(data);
                    this.stuList = data.data
                })
        },
        // 详细信息函数
        handleClose(){
            this.dialogVisible = false;
            this.student = {...this.initialization()}
        },  
        handleCreateClose(){
            
            // this.student = {...this.initialization()}
             this.$refs['createStudentRef'].validate((valid) => {
                if (valid) {
                    // alert('submit!');
                    this.dialogCreateVisible = false;
                    API.addStudent(this.student).then(({data})=>{
                        console.log(data);
                        if(data.code === 200)this.$message({message:data.msg,type:"success"})
                        else this.$message({message:data.msg,type:"error"})
                        this.getStudentList()
                    })
                } else {
                    console.log('error submit!!');
                    this.dialogCreateVisible = false;
                    this.student = {...this.initialization()}
                    return false;
                }
            });
        },  
        sex(stuSex){
            return stuSex !== '0'?'男':(stuSex === ''?'':'女');
        },
        // 搜索功能
        onSubmit(){
            console.log(this.form);
            if(this.form.stuName != ''){
                API.getStuConditions(this.form).then(({data}) => {
                    console.log(data);
                    this.stuList = data.data
                })  
            }else {
                this.getStudentList();
                
            }
            
        },
        onCreate(){
            this.dialogCreateVisible = true;
        },
        initialization(){
            return {
                card:null,
                img:null,
                mail:null,
                stuBirthday:null,
                stuCalss:null,
                stuId:null,
                stuName:null,
                stuPassword:null,
                stuPhone:null,
                stuSex:null,
            }
        }
  },
};
</script>
<style scoped>
.box {
    margin-top: 20px;
    padding: 0 30px;
}
.form_box {
    display: flex;
        justify-content: space-between;
}


/* 移除默认的边框和背景色 */
.demo-ruleForm /deep/ .el-input__inner{
  border: 1px solid transparent;
  border-bottom: 1px solid #606266;
  /* background-color: transparent; */
  box-shadow: none; /* 移除阴影 */
  border-radius:0;
}

/* 添加底部横线 */
.el-input__inner:focus,
.el-input__inner:hover {
  border-bottom: 1px solid black; /* 底部黑色横线 */
  outline: none; /* 移除默认焦点轮廓 */
}

.demo-ruleForm {
    position: relative;
}
/* .block {
    position: absolute;
    right: 10px;
    top: 0px;
} */
</style>