import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)


const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

import HelloWorld from '@/views/Home.vue'
import Login from '@/views/Login.vue'
import HomePage from '@/views/HomePage'
import ChangePassword from '@/views/ChangePassword'
import PersonalInfo from '@/views/PersonalInfo'
import StudentManage from '@/views/StudentManage'
import TeacherManage from '@/views/TeacherManage'
import NoticeManage from '@/views/noticeManage'
import PsyAppointment from '@/views/Appointment'
import CreateExam from '@/views/CreateExam'
import ExamManage from '@/views/ExamManage'
import ExamRequirements from '@/views/ExamRequirements'
import DuringExam from '@/views/DuringExam'
import ScoreManae from '@/views/ScoreManae'


//实现各个页面跳转
export default new VueRouter({
    routes:[
        {
            path: '/',
            component:Login
        },
        {
            path: "/home",
            component:HelloWorld,
            name: 'home',
            children:[
                {
                    path:"homepage",
                    component: HomePage,
                    meta:{
                        title: "首页"   
                    },      
                },
                {
                    path:"chpass",
                    component: ChangePassword,
                    meta:{
                        title: "修改密码"   
                    },
                },
                {
                    path:"perinfo",
                    component: PersonalInfo,
                    meta:{
                        title: "个人信息"   
                    },
                },
                {
                    path:"stuManage",
                    component: StudentManage,
                    meta:{
                        title: "学生管理"   
                    },
                },
                {
                    path:"teaManage",
                    component: TeacherManage,
                    meta:{
                        title: "教师管理"   
                    },
                },
                {
                    path:"notManage",
                    component: NoticeManage,
                    meta:{
                        title: "通知管理"   
                    },
                },
                {
                    path:"psy",
                    component: PsyAppointment,
                    meta:{
                        title: "预约管理"   
                    },
                },
                {
                    path:"addExam",
                    component: CreateExam,
                    meta:{
                        title: "新增试卷"   
                    },
                },
                {
                    path:"examManage",
                    component: ExamManage,
                    meta:{
                        title: "试卷管理"   
                    },
                },
                {
                    path:"ScoreManae",
                    component: ScoreManae,
                    meta:{
                        title: "成绩管理"   
                    },
                },

            ]
        },
        {
            path:"/examRequire",
            component: ExamRequirements,
            meta:{
                title: "考试须知"   
            },
        },
        {
            path:"/duringExam",
            component: DuringExam,
            meta:{
                title: "考试中"   
            },
        },
        

    ]
})