<template>
  <div class="Inbox">
        <div class="header">考试
            <el-button type="primary" class="anniu" @click="onSubmit">确认提交</el-button>
        </div>
        <div class="hclass">

            <div class="h_left">
                <h2 class="le_ct">{{this.$route.query.exam.examName}}</h2>
                 <el-statistic
                    ref="statistic"
                    class="time_box"
                    format="HH:mm:ss"
                    :value="datetiem"
                    time-indices    
                >
                </el-statistic>
                <div class="info">
                    <p>姓名：{{this.$store.state.user.name}}</p>
                    <p>学号：{{this.$store.state.user.id}}</p>
                </div>
                <div class="info">
                    <p>题量：{{this.subjectList.length}}</p>
                    <p>满分：{{this.$route.query.exam.examScore}}</p>
                </div>
            </div>
            
            <div class="h_center">
                <div>
                    <span class="fen">(判断题，{{this.subject.subScore}}分)</span><br>
                    <span>{{this.subject.subContent}}</span>
                </div>
                <div class="select_answer">
                    <div style="margin-bottom: 18px;">
                        <span :class="{'answer-button':true,'anxie':buttAnswerCSS('A')}" @click="getAnswer('A')" >
                            <span class="button-number">A</span>
                        </span>
                        <span class="answer_txt">正确</span>
                    </div>
                    <div>
                        <span :class="{'answer-button':true,'anxie':buttAnswerCSS('B')}" @click="getAnswer('B')">
                            <span class="button-number">B</span>
                        </span>
                        <span class="answer_txt">错误</span>
                    </div>
                   
                </div>
            </div>

            <!-- <div class="h_right"> -->
                <div class="rit_box">
                    <div :class="{'round-button':true,'anxie':subject.id === value.id,'ook':value.myAnswer}" v-for="(value,key) in subjectList" :key="key" @click="upSub(value)">
                        <span class="button-number">{{key + 1}}</span>
                    </div>
                </div>
            <!-- </div> -->
        </div>
  </div>
</template>

<script>
import API from '@/api/studentApi'



export default {
  name: 'DuringExam',
    computed:{
        datetiem(){
            console.log(111111,this.$route.query.exam.examDuration);
            return Date.now() +  this.$route.query.exam.examDuration * 60 * 1000 // 毫秒
        },
        
    },
  data() {
    return {
        subjectList:[],
        subject:{
            id:"",
            examId:"",
            subContent:"",
            subScore:"",
            subAnswer:"",
        },
    };
  },

  mounted() {
    console.dir(this.$route.query.exam);
    // this.deadline2 = Date.now() + this.$route.query.exam.examDuration * 60
    API.getSubByExamId(this.$route.query.exam.id).then(({data})=>{
        console.log(data);
        this.subjectList = data.data
        for (let index = 0; index < this.subjectList.length; index++) {
            this.$set(this.subjectList[index],"myAnswer","")
        }
        this.subject = this.subjectList[0]
    })
  },

  methods: {
    upSub(value){
        this.subject = value
    },
    getAnswer(answer){
        console.log("yyyy",answer);
        this.subject.myAnswer = answer
        console.log("yyyu",this.subject.myAnswer === 'A');
    },
    buttAnswerCSS(answer){
        if(this.subject.myAnswer === answer)return true;
        else return false;
    },
    onSubmit(){
        let flag = true 
         for (let index = 0; index < this.subjectList.length; index++) {
            if(this.subjectList[index].myAnswer === ''){
                flag = false;
                break;
            }
        }
        if(!flag){
            this.tips()
        }else {
            this.scoringMeth();
            this.$router.push({path:'/home/examManage'})
            this.$message({
            type: 'success',
            message: '提交成功!'
          });
        }
        console.log(flag);
    },
    tips(){
        this.$confirm('您还有题未做答, 是否继续?', '确认提交', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '提交成功!'
          });
            this.scoringMeth();
            this.$router.push({path:'/home/examManage'})
        }).catch(() => {
        //   this.$message({
        //     type: 'info',
        //     message: '已取消删除'
        //   });          
        });
    },
    scoringMeth(){
        let examRecord = {
            examId: '',
            stuId: '',
            total:0,
            time: '',
            analysis: '',
        }
        examRecord.examId =  this.$route.query.exam.id
        examRecord.stuId = this.$store.state.user.id
        examRecord.total = this.getTotalScore()
        examRecord.time = "2024-02-25 12:00:00";
        console.log(examRecord);
        API.addExamRecord(examRecord).then(({data})=>{
            console.log(data);
        })
    },
    getTotalScore(){   // 获取总分数
        let sum = 0;
        this.subjectList.forEach(obj => {
            if(obj.myAnswer != "" && obj.myAnswer === obj.subAnswer){
                sum += obj.subScore
            }
        })
        return sum;
    }

  },
};
</script>
