<template>
  <div class="cont_11">
    <!-- -->    
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="旧密码" prop="oldpass">
            <el-input type="password" v-model="ruleForm.oldpass" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="新密码" prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
    </el-form>
  </div>
</template>

<script>
import API from '@/api/studentApi'

export default {
  name: 'changePassword',
    
  data() {
     var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        console.log(123);
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      var oldPassword = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            }
            console.log(this.$store.state.user.id,this.ruleForm.oldpass);
            API.isLoginStare(this.$store.state.user.id,this.ruleForm.oldpass).then(({data}) => {
                if(data.code === 200){
                    callback();
                }else callback(new Error(data.msg));
            })
      }
    return {
        ruleForm: {
         oldpass:'',
          pass: '',
          checkPass: '',
        },
        rules: {
            oldpass:[
                {validator: oldPassword, trigger: 'blur'}
            ],
            pass: [
            { validator: validatePass, trigger: 'blur' }
            ],
            checkPass: [
            { validator: validatePass2, trigger: 'blur' }
            ],
        }
    };
  },

  mounted() {
    
  },

  methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
            if (valid) {
                // alert('submit!');
                API.changePass(this.$store.state.user.id,this.ruleForm.pass).then(({data})=>{
                    if(data.code === 200)this.$message({message:"修改成功！",type:"success"})
                    else this.$message({message:"修改失败！",type:"error"})
                })
                this.resetForm("ruleForm")
            } else {
                console.log('error submit!!');
                return false;
            }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
  },
};
</script>

<style >
    .cont_11 {
        padding-top: 40px;
        width: 400px;
    }
</style>