<template>

    <div class="box">
        <el-form ref="form" :inline="true" :model="form" class="demo-form-inline form_box" >
            <div>
                <el-form-item label="姓名">
                 <el-col :span="40">
                    <el-input v-model="form.teaName" style="width: 100%;"></el-input>
                 </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </div>
            <div>
                <el-form-item v-show="!stateRole0">
                    <el-button type="primary" @click="onCreate">新建</el-button>
                </el-form-item>
            </div>
        </el-form>

            <div>
                <el-table :data="teaList.slice((currentPage - 1) * pagesize, currentPage * pagesize)" 
            style="width:100%;font-size: 12px;" >

                 <el-table-column label="教师号" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.teaId}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="姓名" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px;">{{scope.row.teaName}}</span>
                    </template>
                </el-table-column>

               <el-table-column label="电话" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.teaPhone}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="邮箱" >
                    <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.mail}}</span>
                        </template>
                </el-table-column>

                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button 
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)" 
                            type="primary" icon="el-icon-info" circle ></el-button>
                        <el-button 
                            size="mini"
                            v-show="!stateRole0"
                            @click="handleDelete(scope.$index, scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
                            <el-tag :type="getTxtState(scope.row.ment).type" @click="reservation(scope.row)" style="margin-left: 10px;" v-show="stateRole0">{{getTxtState(scope.row.ment).txt}}</el-tag>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 15, 20]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="teaList.length">    
             </el-pagination> 

             <!-- 详细信息 -->
        <el-dialog
            title="教师信息详情"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            
            <el-form status-icon :model="book" ref="modifyForm" label-width="60px" class="demo-ruleForm">
                <div class=""><el-avatar shape="square" :size="50" :src="squareUrl"></el-avatar></div>
                
                <el-form-item label="姓名" prop="pass" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="teacher.teaName"
                        autocomplete="off" class="txt_input"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="性别" prop="pass" style="width:95%">
                    
                    <el-input type="txt" v-model="formattedSex"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="教师号" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="teacher.teaId"
                    autocomplete="off" ></el-input>
                </el-form-item>

                <el-form-item label="年月日" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="teacher.teaBirthday"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="电话" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="teacher.teaPhone"
                    autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="teacher.mail"
                    autocomplete="off"></el-input>
                </el-form-item>
                
            </el-form>
        </el-dialog> 


         <el-dialog
            title="预约信息详情"
            :visible.sync="dialoCreateVisible"
            width="30%"
            :before-close="AAhandleClose">
            
            <el-form status-icon :model="ment" ref="modifyForm" label-width="70px" class="Yu-ruleForm">

                <el-form-item label="预约时间" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="ment.time"
                    autocomplete="off"></el-input>
                </el-form-item>
                <div class="block">
                    <span class="demonstration">预约时间</span>
                    <el-date-picker
                    v-model="ment.time"
                    type="datetime"
                    placeholder="选择日期时间"
                    format="yyyy-MM-dd HH:mm:ss"
                        value-format="yyyy-MM-dd HH:mm:ss">
                    </el-date-picker>
                </div>
                <el-form-item label="备注" prop="pass" style="width:95%">
                    <el-input type="txt" v-model="ment.notes"
                    autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item v-show="this.ment.state == 0">
                    <el-button type="primary" @click="addAppointment" >确定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog> 
    </div>
  </div>
</template>

<script>
import API from '@/api/studentApi'

export default {
  name: 'TeacherManage',
computed: {
    formattedSex: {
      get() {
        if(this.teacher.teaSex === 0 ){
            return '男'
        }else if(this.teacher.teaSex === 1){
            return '女'
        }else {
            return '';
        }
      },
      set(value) {
        this.teacher.teaSex = value === '男' ? 0 : 1;
      }
    },
      stateRole0(){   // 学生
      return Number(this.$store.state.user.role) === 0
    },
    stateRole1(){   // 教师
      return Number(this.$store.state.user.role) === 1
    },
    stateRole2(){   //管理
      return Number(this.$store.state.user.role) === 2
    },
  },
  data() {
    return {
        form:{
            teaName:""
        },
        teaList:[],
        // 分页
        currentPage: 1, //初始页
        pagesize: 10, //    每页的数据,
        dialogVisible:false,
        dialoCreateVisible:false,
        teacher:{
            img:'',
            mail:'',
            resume:'',
            teaBirthday:'',
            teaId:'',
            teaName:'',
            teaPassword:'',
            teaPhone:'',
            teaSex:'',
        },
        squareUrl: "https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png",
        ment:{
            id:'',
            notes:'',
            state:0,
            stuId:'',
            stuName:'',
            teaId:'',
            teaName:'',
            time:'',
        }
    };
  },

  mounted() {
        this.getTeacherAll()
  },

  methods: {
    onSubmit(){
        console.log(this.form);
        if(this.form.teaName != ''){
            API.getTeaConditions(this.form).then(({data}) => {
                console.log(data);
                this.teaList = data.data
            })  
        }else {
            this.getTeacherAll();
        }
    },
    onCreate(){
      
    },
    getTeacherAll(){
        API.getTeaAll().then(({ data }) => {
        console.log(data);
        let dataList = data.data;
        let promises = dataList.map(item => {
            return API.getState({
                stuId: this.$store.state.user.id,
                teaId: item.teaId
            }).then(({ data }) => {
                console.log(data);
                item.ment = data.data;
                return item;  // 返回更新后的item
            });
        });
        Promise.all(promises).then(() => {
            this.teaList = dataList;  // 所有异步操作完成后，更新this.teaList
        });
    });
    },
    handleSizeChange (size) {
            console.log(size,'size');
            this.pagesize = size;
            console.log(this.pagesize); //每页下拉显示数据
        },
        handleCurrentChange (currentPage) {
            console.log(currentPage,'currentPage');
            this.currentPage = currentPage;
            console.log(this.currentPage); //点击第几页
        },
        
        handleEdit(index, row) {
            console.log(index, row);
            this.dialogVisible = true;
            this.teacher = row
            console.log(this.teacher);
        },
        handleDelete(index, row) {
            console.log(index, row);
            API.deleteTea(row.teaId).then(({data})=>{
                console.log(data);
                this.$message({message:data.msg,type:"success"})
            })
            let key = this.teaList.findIndex(item => item.teaId === row.teaId);
            if (key !== -1) {
                this.teaList.splice(key, 1);
            }
        },
        handleClose(){
            this.dialogVisible = false;
        }, 
        reservation(row){
            if(!row.ment){
                this.dialoCreateVisible = true;
                this.ment.teaId = row.teaId
                this.ment.stuId = this.$store.state.user.id
                this.ment.stuName = this.$store.state.user.name
                this.ment.teaName = row.teaName
            }else {
                
                this.ment.time = row.ment.time
                this.ment.notes = row.ment.notes
                this.ment.state = row.ment.state
                this.dialoCreateVisible = true;
            }

        },
        AAhandleClose(){
            this.dialoCreateVisible = false;
            this.ment = {    // 初始化
                id:'',
                notes:'',
                state:0,
                stuId:'',
                stuName:'',
                teaId:'',
                teaName:'',
                time:'',
            }
        },
        // 添加
        addAppointment(){
            console.log(this.ment);
            API.addMent(this.ment).then(({data})=>{
                console.log(data);
                if(data.code === 200){
                    this.$message({message:'预约成功',type:"success"})
                    this.dialoCreateVisible = false;
                    this.getTeacherAll()
                }else{
                    this.$message({message:'预约失败',type:"error"})
                }
                this.ment = {    // 初始化
                        id:'',
                        notes:'',
                        state:0,
                        stuId:'',
                        stuName:'',
                        teaId:'',
                        teaName:'',
                        time:'',
                    }

            })
        },
        getTxtState(ment){  // warning 
            let state = null;
            if(ment != null){
                state = ment.state
            }
            console.log(state);
            if(state === 0){
                return {
                    type: 'warning',
                    txt: '未受理'
                }
            }else if(state === 1){
                return {
                    type: 'danger',
                    txt: '预约失败'
                }
            }else if(state === 2){
                return {
                    type: 'success',
                    txt: '预约成功'
                }
            }else {
                return{
                    type: 'info',
                    txt: '预约'
                }
            }
        }
  },
};
</script>

<style scoped>
.box {
    margin-top: 20px;
    padding: 0 30px;
}
.form_box {
    display: flex;
        justify-content: space-between;
}
.demo-ruleForm /deep/ .el-input__inner{
  border: 1px solid transparent;
  border-bottom: 1px solid #606266;
  box-shadow: none;
  border-radius:0;
}

/* 添加底部横线 */

.demo-ruleForm {
    position: relative;
}
.block {
    text-align: left;
    margin-bottom: 20px;
    
}
.demonstration {
    padding-right: 12px;
}
</style>