<template>
  <div class="login"> 
    <div class="box">
        <!-- 位置 -->
       <div class="pos">  
            <div class="title">
                大学生心理健康系统
                <div class="chaoz">登录</div>
            </div>
            <div class="content">
                <el-form ref="form" :model="form" label-width="50px" :rules="rules">
                    <el-form-item label="学号"  prop="id" >
                        <el-input v-model="form.id"></el-input>
                    </el-form-item>
                    <el-form-item label="密码"  prop="password"  >
                        <el-input v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item label=""> 
                        <!-- v-model="form.role  绑定数据 -->
                        <el-radio-group v-model="form.role">
                        <el-radio label="0">学生</el-radio>
                        <el-radio label="1">教师</el-radio>
                        <el-radio label="2">管理员</el-radio>
                        </el-radio-group>
                    </el-form-item> 
                    <el-form-item class="butt">
                        <el-button type="primary" @click="onSubmit">登录</el-button>
                        <el-button @click="onCansel">重置</el-button>
                    </el-form-item>
                </el-form>
                
            </div>
            
       </div>
    </div>
  </div>
</template>



<script>
// vue --> vuecli vue脚手架，脚手架意思是项目级别的vue
import studentApi from "@/api/studentApi"
export default {
  name: 'PsychologyServerLogin',
  data() {    // 存放页面数据，每个页面单独独立
    return {
      form: {
          id: '',
          password:'',
          role:'1'
      } ,
      rules:{
        id:[
            { required: true, message: '请输入学号', trigger: 'blur' },
            // { min: 6, max: 14, message: '长度在 6 到 14 个字符', trigger: 'blur' }
        ],
        password:[
            { required: true, message: '请输入密码', trigger: 'blur' },
            // { min: 6, max: 11, message: '长度在 6 到 11 个字符', trigger: 'blur' }
        ]
      }
    };
  },

  mounted() {
    
  },

  methods: {
    onSubmit() {
        console.log('submit!');
        studentApi.login(this.form).then(({data}) => {
            console.log(data);
            if(data.code == 200){
                let users = data.data;
                users.role = this.form.role;
                this.$store.commit("SETUSER",users);
                // 进入主页
                this.$message({message:"登录成功！",type:"success",duration:1000,onClose:()=>{
                    this.$router.push("/home/homepage")
                }})
            }else {
                this.$message({message:data.msg,type:"error"})
                this.onCansel();
            }
        })
    },
    onCansel(){
        this.form.id = "";
        this.form.password = "";
    }
  },
};
</script>

<style scoped>
    .login {
        width: 100vw;
        height: 100vh;
        background: url("../assets/img/backlogin.jpg") no-repeat;
        /* background-size: 100%; */
    }
    .box {
        height: 100%;
        width: 450px;
        position:absolute;
        right: 0;
        background-color: rgb(250,239,221);
        padding: 50px 40px 50px 40px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .pos {
        width: auto;
        height: auto;
   
    }
    .title {
        font-size: 32px;
        text-align: center;
        /* margin-top: calc(100%); */
    }
    .title .chaoz {
        font-size: 28px;
        margin-top: 10px;

    }
    .content {
        margin-top: 30px;
    }
    .butt {
        /* text-align: center; */
        display: flex;
        justify-content: space-around;
    }
</style>