<template>
  <div class="info_box">
    <!-- -->
    <el-form ref="form" :model="user" label-width="100px">
        <!-- <el-form-item label="学号">
            <el-input v-model="form.id"></el-input>
        </el-form-item> -->

        <el-form-item label="姓名" v-if="user.name" >
            <el-col :span="11">
            <el-input v-model="form.name" style="width: 100%;"></el-input>
            </el-col>
        </el-form-item>

        <el-form-item label="身份证" v-if="user.card" >
            <el-col :span="11">
            <el-input v-model="form.card" style="width: 100%;"></el-input>
            </el-col>
        </el-form-item>

        <el-form-item label="联系电话" v-if="user.phone" >
            <el-col :span="11">
            <el-input v-model="form.phone" style="width: 100%;"></el-input>
            </el-col>
        </el-form-item>

        <el-form-item label="班级" v-if="user.calss" >
            <el-col :span="11">
            <el-input v-model="form.calss" style="width: 100%;"></el-input>
            </el-col>
        </el-form-item>

        <el-form-item label="邮箱" v-if="user.mail" >
            <el-col :span="11">
            <el-input v-model="form.mail" style="width: 100%;"></el-input>
            </el-col>
        </el-form-item>


        <el-form-item label="出生日期" v-if="this.user.birthday" >
            <el-col :span="11">
            <el-date-picker type="date" 
                placeholder="选择日期" 
                v-model="form.birthday" 
                style="width: 100%;"
                format="yyyy 年 MM 月 dd 日"
                value-format="yyyy-MM-dd"></el-date-picker>
            </el-col>
        </el-form-item> 

        <el-form-item label="简介" prop="desc" v-if="this.user.resume">
            <el-col :span="11">
            <el-input type="textarea" v-model="form.resume"></el-input>
            </el-col>
        </el-form-item>

        <el-form-item >
            <el-col :span="11">
                <el-button type="primary" @click="submitForm()">确认修改</el-button>
                <el-button @click="resetForm()">重置</el-button>
            </el-col>
        </el-form-item>

    </el-form>
  </div>
</template>

<script>

import API from '@/api/studentApi'
import { mapState } from 'vuex';
export default {
    name: 'personalInfo',
    computed:{
        ...mapState({
            user: state => state.user
        })
    },
    data() {
        return {
            form:{
               name:"",
               card:"",
               phone:"",
               calss:"",
               mail:"",
               birthday:"",
               resume:'',  //教师简介
            }
        };
    },
    mounted() {
        
    },
    created(){
        this.initializeForm()
    },
    methods: {
        submitForm(){
            this.resetProperties()
        },
        resetForm(){
            this.initializeForm();
        },
        initializeForm(){
            this.form.name = this.user.name || '';
            this.form.card = this.user.card || '';
            this.form.phone = this.user.phone || '';
            this.form.calss = this.user.calss || ''; // 注意：这里可能是个拼写错误，应该是 'class'
            this.form.mail = this.user.mail || '';
            this.form.birthday = this.user.birthday || '';
            this.form.resume = this.user.resume || '';
        },
        
        resetProperties(){
            console.log(this.$store.state.user.role === "0",this.$store.state.user.role);
            if(this.$store.state.user.role === "0"){
                let student ={}
                student.card = this.form.card;
                student.stuPhone = this.form.phone
                student.stuCalss = this.form.calss
                student.mail = this.form.mail
                student.stuBirthday = this.form.birthday
                student.stuId = this.user.id
                student.stuName = this.form.name 
                API.updateUser(student).then(({data})=>{
                    console.log(data);
                    data.data.role = this.user.role
                    this.$message({message: '修改成功',type:"success"})
                    this.$store.commit('SETUSER',data.data)
                })
  
            }else if(this.$store.state.user.role === "1"){
                let teaher = {};
                teaher.card = this.form.card;
                teaher.teaPhone = this.form.phone
                teaher.teaCalss = this.form.calss
                teaher.mail = this.form.mail
                teaher.teaBirthday = this.form.birthday
                teaher.teaId = this.user.id
                teaher.teaName = this.form.name 
                teaher.resume = this.form.resume 
                API.updateTeaUser(teaher).then(({data})=>{
                    console.log(data);
                    data.data.role = this.user.role
                    this.$message({message: '修改成功',type:"success"})
                    this.$store.commit('SETUSER',data.data)
                })
            }else if(this.$store.state.user.role === "2"){
                let admin = {}
                admin.name = this.form.name;
                admin.id =this.user.id;
                API.updateAdminUser(admin).then(({data})=>{
                    console.log(data);
                    data.data.role = this.user.role
                    this.$message({message: '修改成功',type:"success"})
                    this.$store.commit('SETUSER',data.data)
                })
            }
        }
    },
};
</script>

<style scoped>
.info_box {
    padding-top: 40px;
}
</style>