<template>
  <div class="header">
    <div class="title">
        大学生心理健康系统
    </div>
    <div class="right">
            <span class="role">{{this.roleName}} {{this.$store.state.user.name}}</span>
            <span class="butt" @click="logOut">退出登录</span>

        
    </div>
  </div>
</template>

<script>
export default {
  name: 'pyHeader',

    data() {
        return {

        };
    },
    computed:{
        roleName:function(){
            let role = this.$store.state.user.role;
            if(role == 0)return "同学";
            if(role == 1)return "老师";
            else return "管理员";
        }
    },
    mounted() {

    },

    methods: {
        logOut(){
            this.$message({message:"退出成功!",type:"success",duration:800,onClose:()=>{
                this.$store.commit("CLEANUSER")
                this.$router.push("/")
            }})
        }
    },
    };
</script>
<style scoped>
    .header {
        position: relative;
    }
    .title {
        font-size: 30px;
        color: #fff;
        font-weight: 700;
        text-align: center;
        letter-spacing: 8px;
    }
    .right {
        height: 100%;
        color: #fff;
        /* width: 200px; */
        /* background-color: red; */
        position: absolute;
        right: 0;
        top: 0;
        font-size: 14px;
    }
    .role{
        margin-right: 10px;
    }
    .butt {
        cursor: pointer;
    }
</style>