<template>
      <div class="box">
        <el-form ref="form" :inline="true" :model="form" class="demo-form-inline form_box" >
            <div>
                <el-form-item label="学生id">
                 <el-col :span="40">
                    <el-input v-model="form.stuName" style="width: 100%;"></el-input>
                 </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </div>

        </el-form>

        
        <!-- 表格 -->
        <div>
            <el-table :data="scoreList.slice((currentPage - 1) * pagesize, currentPage * pagesize)" 
        style="width:100%;font-size: 12px;" >

                 <el-table-column label="id" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.id}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="学生id" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px;">{{scope.row.stuId}}</span>
                    </template>
                </el-table-column>

               <el-table-column label="试卷id" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.examId}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="得分" >
                    <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.total}}</span>
                        </template>
                </el-table-column>

                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <!-- <el-button 
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)" type="primary" icon="el-icon-info" circle ></el-button> -->
                        <el-button 
                            size="mini"
                            @click="handleDelete(scope.$index, scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
                    </template>
                </el-table-column>
            </el-table>
             <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 15, 20]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="scoreList.length">
             </el-pagination>  

        </div>


      </div>
</template>

<script>
import API from '@/api/studentApi'
export default {
  name: 'ScoreMan',

  data() {
    return {
        form:{
            stuName:""
        },
        scoreList:[],
        // 分页
        currentPage: 1, //初始页
        pagesize: 10, //    每页的数据,
        // 有哪些书架 名/id
        score:{
            analysis:'',
            examId:'',
            id:'',
            stuId:'',
            time:'',
            total:'',

        }
    };
  },

  mounted() {
        this.getAll()
  },

  methods: {
         handleSizeChange (size) {
            console.log(size,'size');
            this.pagesize = size;
            console.log(this.pagesize); //每页下拉显示数据
        },
        handleCurrentChange (currentPage) {
            console.log(currentPage,'currentPage');
            this.currentPage = currentPage;
            console.log(this.currentPage); //点击第几页
        },
        getAll(){
            API.getRecordAll().then(({data})=>{
                console.log(data);
                this.scoreList = data.data
            })
        },
        //  handleEdit(index, row) {
        //     console.log(index, row);
        //     this.dialogVisible = true;
        //     this.student = row
        //     console.log(this.student);
        // },
        handleDelete(index, row) {
            console.log(index, row);
            API.deleteRecordById(row.id).then(({data})=>{
                console.log(data);
                this.$message({message:data.msg,type:"success"})
            })
            // 根据id删除对象数组中的对象
            let key = this.scoreList.findIndex(item => item.id === row.id);
            if (key !== -1) {
                this.scoreList.splice(key, 1);
            }
        },
        onSubmit(){
            if(this.form.stuName !== ''){
                API.getRecordByStuId(this.form.stuName).then(({data})=>{
                this.scoreList = data.data
            })
            }else {
                this.getAll()
            }
        }
  },
};
</script>

<style scoped>
.box {
    margin-top: 20px;
    padding: 0 30px;
}
.form_box {
    display: flex;
        justify-content: space-between;
}
</style>