<template>
      <div class="box">
            <el-form ref="form" :inline="true" :model="form" class="demo-form-inline form_box" >
                <div>
                    <el-form-item label="试卷名">
                    <el-col :span="40">
                        <el-input v-model="form.teaName" style="width: 100%;"></el-input>
                    </el-col>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">查询</el-button>
                    </el-form-item>
                </div>
                <!-- <div>
                    <el-form-item v-show="!stateRole0">
                        <el-button type="primary" @click="onCreate">新建</el-button>
                    </el-form-item>
                </div> -->
            </el-form>

             <!-- 表格 -->
        <div>
            <el-table :data="examList.slice((currentPage - 1) * pagesize, currentPage * pagesize)" 
        style="width:100%;font-size: 12px;" >

                 <el-table-column label="id" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.id}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="试卷名" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px;">{{scope.row.examName}}</span>
                    </template>
                </el-table-column>

               <el-table-column label="总分" >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.examScore}}</span>
                    </template>
                </el-table-column>

                <el-table-column label="状态" >
                    <template slot-scope="scope">
                            <!-- <span style="margin-left: 10px">{{ scope.row.examState}}</span> -->
                            <el-tag :type="getTxtState(scope.row).type">{{getTxtState(scope.row).txt}}</el-tag>
                        </template>
                </el-table-column>

                <el-table-column label="操作" v-if="stateRole1 || stateRole2">
                    <template slot-scope="scope" >
                        <el-button 
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)" type="primary" icon="el-icon-info" circle ></el-button>
                        <el-button 
                            size="mini" 
                            @click="handleXiuGai(scope.$index, scope.row)" type="warning" icon="el-icon-edit" circle></el-button>
                        <el-button 
                            size="mini"
                            @click="handleDelete(scope.$index, scope.row)" type="danger" icon="el-icon-delete" circle></el-button>
                    </template>
                </el-table-column>
                <el-table-column v-if="stateRole0" label="操作">
                    <template slot-scope="scope" >
                        <el-button plain size="mini" @click="startExam(scope.$index, scope.row)">开始考试</el-button>
                    </template>
                </el-table-column>

            </el-table>
             <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 15, 20]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="examList.length">
             </el-pagination>  
        </div>

                <!-- 详细信息 -->
        <el-dialog
            title="试卷详细详情"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            
            <el-form status-icon :model="exam" ref="modifyForm" label-width="70px" class="demo-ruleForm">

                <el-form-item label="id" prop="" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="exam.id"
                        autocomplete="off" class="txt_input" disabled></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="试卷名" prop="" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="exam.examName"
                        autocomplete="off" class="txt_input" disabled></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="考试时长" prop="" >
                    <el-col  style="width:95%">
                        <el-input type="txt" v-model="exam.examDuration"
                        autocomplete="off" class="txt_input" disabled></el-input>
                    </el-col>
                </el-form-item>

                <el-form-item label="总分" prop="" style="width:95%">
                    <el-input type="txt" v-model="exam.examScore"
                    autocomplete="off" disabled></el-input>
                </el-form-item>

                <el-form-item label="考试状态"  style="width:95%">
                    <el-select v-model="exam.examState" style="width:95%">
                        <el-option label="未开始" :value="0"></el-option>
                        <el-option label="已开始" :value="1"></el-option>
                    </el-select>
                </el-form-item>


            </el-form>
        </el-dialog>


        <!-- <el-dialog
            title="试卷详细详情"
            :visible.sync="dialogVisibleStartExam"
            width="30%"
            :before-close="handleCloseTxt">
            
            
        </el-dialog> -->


      </div>
</template>

<script>
import API from '@/api/studentApi'

export default {
  name: 'ExamManage',
  computed:{
      stateRole0(){   // 学生
      return Number(this.$store.state.user.role) === 0
    },
    stateRole1(){   // 教师
      return Number(this.$store.state.user.role) === 1
    },
    stateRole2(){   //管理
      return Number(this.$store.state.user.role) === 2
    },
  },
  data() {
    return {
       form:{
            teaName:""
        },
        examList: [],
        currentPage: 1, //初始页
        pagesize: 10, //    每页的数据,
        dialogVisible:false,
        exam:{
            id:'',
            examName:'',
            examDuration:'',
            examScore:'',
            examState:0,
        },
        dialogVisibleStartExam:false
        
    };
  },

  mounted() {
    this.getExamList()
  },

  methods: {
        getExamList(){
            API.getExamList().then(({data})=>{
                console.log(data);
                this.examList = data.data   
            })
        },
        handleSizeChange (size) {
            console.log(size,'size');
            this.pagesize = size;
            console.log(this.pagesize); //每页下拉显示数据
        },
        handleCurrentChange (currentPage) {
            console.log(currentPage,'currentPage');
            this.currentPage = currentPage;
            console.log(this.currentPage); //点击第几页
        },

        handleEdit(index, row) {
            console.log(index, row);
            this.dialogVisible = true;
            this.exam = {...row}
            // console.log(this.student);
        },
        handleDelete(index, row) {
            console.log(index, row);
            API.deleteExam(row.id).then(({data})=>{
                console.log(data);
                this.$message({message:data.msg,type:"success"})
            })
            // // 根据id删除对象数组中的对象
            let key = this.examList.findIndex(item => item.id === row.id);
            if (key !== -1) {
                this.examList.splice(key, 1);
            }
        },
        handleXiuGai(index, row){
            console.log(index,row);
            this.$router.push({ path: '/home/addExam', query: { examId: row.id }});
        },
        onSubmit(){
            if(this.form.teaName === ""){
                this.getExamList()
            }else {
                API.getExamListByName(this.form.teaName).then(({data})=>{
                    this.examList = data.data
                })
            }
        },
        handleClose(){
            let myfleg = false;
            this.examList.forEach(obj =>{
                if(obj.id === this.exam.id){
                    if(obj.examState !== this.exam.examState){
                        myfleg = true
                    }
                }
            })
            if(myfleg){
                let obj = {
                    id:this.exam.id,
                    examState:this.exam.examState
                }
                API.updateExamState(obj).then(({data})=>{
                    console.log(data);
                    this.$message({message:"修改成功！",type:"success"})
                    this.getExamList()
                })
            }
            this.dialogVisible = false;
            this.exam = {...this.initialization()}

        },  
        startExam(index, row){     // 开始考试  
            console.log(index, row);
            // this.dialogVisibleStartExam = true;
            if(row.examState === 0){
                this.$message({message:"考试未开始",type:"error"})
            }else 
            this.$router.push({
                path: '/examRequire',
                query:{
                    exam:row
                }
            })
        },
        // handleCloseTxt(){   // 考试须知框
        //     this.dialogVisibleStartExam = false
        // },
            getTxtState(row){
            if(row.examState === 0){
                return {
                    type: 'warning',
                    txt: '未开始'
                }
            }else if(row.examState === 1){
                return {
                    type: 'success',
                    txt: '已开始'
                }
            }
        },
        initialization(){
            return {
                id:'',
                examName:'',
                examDuration:'',
                examScore:'',
                examState:0,
            }
        }
  },
};
</script>

<style scoped>
.box {
    margin-top: 20px;
    padding: 0 30px;
}
.form_box {
    display: flex;
        justify-content: space-between;
}
</style>