import myAxios from "@/axios";
const config = {
    headers: {
        'Content-Type': 'application/json' // 设置你想要的Content-Type
    }
};
export default{
    login(loginVo){
        let promise = myAxios.post("/login",loginVo);
        return promise;
    },

    isLoginStare(id,pass){
        let promise = myAxios.post("/isLoginStare",{
            "id":id,
            "pass":pass
        });
        return promise;
    },
    changePass(id,pass){
        let promise = myAxios.post("/changePass",{
            "id":id,
            "pass":pass
        });
        return promise;
    },
    getStuAll(){
        let promise = myAxios.get("/students/getAll");
        return promise;
    },
    deleteStu(id){
        let promise = myAxios.get(`/students/deleteStu/${id}`);
        return promise;
    },
    getStuConditions(students){
        let promise = myAxios.post(`/students/getByS`,students);
        return promise;
    },
    // 添加学生
    addStudent(students){
        let promise = myAxios.post(`/students/addStu`,students);
        return promise;
    },

    //教师
    getTeaAll(){
        let promise = myAxios.get("/teachers/getAll");
        return promise;
    },
    deleteTea(id){
        let promise = myAxios.get(`/teachers/deleteTea/${id}`);
        return promise;
    },
    getTeaConditions(teachers){
        let promise = myAxios.post(`/teachers/getByS`,teachers);
        return promise;
    },

    // 通知
    getNotAll(){
        let promise = myAxios.get("/notice/getAll");
        return promise;
    },
    deleteNot(id){
        let promise = myAxios.get(`/notice/deleteNot/${id}`);
        return promise;
    },
    getNoticeByName(name){
        let promise = myAxios.get(`/notice/getByName/${name}`);
        return promise;
    },
    addNotice(notice){
        let promise = myAxios.post(`/notice/addNotice/`,notice);
        return promise;
    },

    // 教师预约管理
    getMentAll(){
        let promise = myAxios.get("/ment/selectAll");
        return promise;
    },
    updateState(id,state){
        let promise = myAxios.get(`/ment/updatePsy/${id}/${state}`);
        return promise;
    },
    addMent(ment){
        let promise = myAxios.post(`/ment/addAppointment`,ment);
        return promise;
    },
    getState(obj){
        let promise = myAxios.post(`/ment/getState`,obj);
        return promise;
    },

    // 修改个人信息
    updateUser(obj){
        let promise = myAxios.post(`/students/updateUser`,obj);
        return promise;
    },
    updateTeaUser(obj){
        let promise = myAxios.post(`/teachers/updateUser`,obj);
        return promise;
    },
    updateAdminUser(obj){
        let promise = myAxios.post(`/admin/updateUser`,obj);
        return promise;
    },

    // 试题
    addExam(obj){
        let promise = myAxios.post(`/exam/addexam`,obj);
        return promise;
    },
    addSubjectList(obj){
        let promise = myAxios.post(`/subject/addSubject`,obj,config);
        return promise;
    },
    getExamList(){
        let promise = myAxios.get(`/exam/getList`);
        return promise;
    },
    updateExamState(exam){
        let promise = myAxios.post(`/exam/updateExam`,exam);
        return promise;
    },
    deleteExam(id){
        let promise = myAxios.get(`/exam/deleteExam/${id}`);
        return promise;
    },
    getExamListByName(name){
        let promise = myAxios.get(`/exam/getExamByName/${name}`);
        return promise;
    },
    getExamListById(id){
        let promise = myAxios.get(`/exam/getExamById/${id}`);
        return promise;
    },
    getSubByExamId(examId){
        let promise = myAxios.get(`/subject/selectSubByExamId/${examId}`);
        return promise;
    },
    updateSubjectList(subList){
        let promise = myAxios.post(`/subject/updateSubList`,subList,config);
        return promise;
    },

    // 考试记录
    addExamRecord(record){
        let promise = myAxios.post(`/examRecords/addRecord`,record);
        return promise;
    },
    getRecordByExamId(obj){
        let promise = myAxios.post(`/examRecords/getRecordBy`,obj);
        return promise;
    },
    getRecordAll(){
        let promise = myAxios.get(`/examRecords/getRecordAll`);
        return promise;
    },
    deleteRecordById(id){
        let promise = myAxios.get(`/examRecords/deleteRecordById/${id}`);
        return promise;
    },
    getRecordByStuId(stuId){
        let promise = myAxios.get(`/examRecords/getRecordByStuId/${stuId}`);
        return promise;
    },

    

}